<template>
    <div class="row justify-content-center mb-3">
        <div class="offset-md-auto col-md-12 col-11">
            <div class="container horizontal-accordion">
                <div class="card choice expand bg-secondary rounded-end rounded-4" @click="choose_spine">
                    <div class="card-body py-0 text-center">
                        <i class="bi bi-play fs-3 align-middle"></i>
                        <br v-if="isMobile">
                        <button type="button" class="py-0 btn btn-secondary" @click="changeAnimation('attack')">attack</button>
                        <button type="button" class="py-0 btn btn-secondary" @click="changeAnimation('cheer')">cheer</button>
                        <button type="button" class="py-0 btn btn-secondary" @click="changeAnimation('daze')">daze</button>
                        <button type="button" class="py-0 btn btn-secondary" @click="changeAnimation('death')">death</button>
                        <button type="button" class="py-0 btn btn-secondary" @click="changeAnimation('deploy')">deploy</button>
                        <button type="button" class="py-0 btn btn-secondary" @click="changeAnimation('flinch')">flinch</button>
                        <button type="button" class="py-0 btn btn-secondary" @click="changeAnimation('move')">move</button>
                        <button type="button" class="py-0 btn btn-secondary" @click="changeAnimation('panic')">panic</button>
                        <div class="py-0 dropdown div-inline">
                            <button class="py-0 btn btn-secondary dropdown-toggle" type="button" data-bs-toggle="dropdown" aria-expanded="false">
                                other
                            </button>
                            <ul class="dropdown-menu">
                                <li><a class="dropdown-item" href="#" @click.prevent="changeAnimation('room-idle')">room-idle</a></li>
                                <li><a class="dropdown-item" href="#" @click.prevent="changeAnimation('room-move')">room-move</a></li>
                                <li><a class="dropdown-item" href="#" @click.prevent="changeAnimation('room-panic')">room-panic</a></li>
                                <li><a class="dropdown-item" href="#" @click.prevent="changeAnimation('room-poke')">room-poke</a></li>
                                <li><a class="dropdown-item" href="#" @click.prevent="changeAnimation('room-shock')">room-shock</a></li>
                                <li><a class="dropdown-item" href="#" @click.prevent="changeAnimation('special-active')">special-active</a></li>
                                <li><a class="dropdown-item" href="#" @click.prevent="changeAnimation('special-passive')">special-passive</a></li>
                            </ul>
                        </div>
                    </div>
                </div>
                <div class="card choice small bg-secondary rounded-start rounded-4" @click="choose_voice">
                    <div class="card-body py-0 text-center">
                        <i class="bi bi-soundwave fs-3 align-middle"></i>
                        <br v-if="isMobile">
                        <button type="button" class="py-0 btn btn-secondary btn-hidden" @click.prevent="playSound(unit.VoiceTapMorningPath, unit.VoiceTapMorning)">Morning</button>
                        <button type="button" class="py-0 btn btn-secondary btn-hidden" @click.prevent="playSound(unit.VoiceTapNoonPath, unit.VoiceTapNoon)">Noon</button>
                        <button type="button" class="py-0 btn btn-secondary btn-hidden" @click.prevent="playSound(unit.VoiceTapEveningPath, unit.VoiceTapEvening)">Evening</button>
                        <button type="button" class="py-0 btn btn-secondary btn-hidden" @click.prevent="playSound(unit.VoiceGreetingPath, unit.VoiceGreeting)">Greeting</button>
                        <button type="button" class="py-0 btn btn-secondary btn-hidden" @click.prevent="playSound(unit.VoiceDeathPath, unit.VoiceDeath)">Death</button>
                        <button type="button" class="py-0 btn btn-secondary btn-hidden" @click.prevent="playSound(unit.VoiceTapDeployPath, unit.VoiceTapDeploy)">Deploy</button>
                        <button type="button" class="py-0 btn btn-secondary btn-hidden" @click.prevent="playSound(unit.VoiceTapVictoryPath, unit.VoiceTapVictory)">Victory</button>
                        <div class="py-0 dropdown div-inline">
                            <button class="py-0 btn btn-secondary dropdown-toggle btn-hidden" type="button" data-bs-toggle="dropdown" aria-expanded="false">
                                Other
                            </button>
                            <ul class="dropdown-menu">
                                <li><a class="dropdown-item" href="#" @click.prevent="playSound(unit.VoiceTap1Path, unit.VoiceTap1)">Tap1</a></li>
                                <li><a class="dropdown-item" href="#" @click.prevent="playSound(unit.VoiceTap2Path, unit.VoiceTap2)">Tap2</a></li>
                                <li><a class="dropdown-item" href="#" @click.prevent="playSound(unit.VoiceUnlockPath, unit.VoiceUnlock)">Unlock</a></li>
                                <li><a class="dropdown-item" href="#" @click.prevent="playSound(unit.VoiceTapLevelUpPath, unit.VoiceTapLevelUp)">LevelUp</a></li>
                                <li><a class="dropdown-item" href="#" @click.prevent="playSound(unit.VoiceUltimatePath, unit.VoiceUltimate)">Ultimate</a></li>
                            </ul>
                        </div>
                        <div id="voice_lan" class="py-0 input-group div-inline w-auto d-none" role="group">
                            <button type="button" class="py-0 btn btn-outline-light btn-sm" :class="{ active: voice_lan === 1 }" @click="voice_lan = 1">ko</button>
                            <button type="button" class="py-0 btn btn-outline-light btn-sm" :class="{ active: voice_lan === 2 }" @click="voice_lan = 2">jp</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <div class="row justify-content-center mb-3">
        <div class="offset-md-auto col-md-12 col-11">
            <div id="soundAlert" class="alert alert-warning d-none" role="alert">
                pay attention to volume!
            </div>
        </div>
    </div>
    <div class="row justify-content-center mb-3">
        <div class="offset-md-auto col-md-12 col-11">
            <div id="voice_text" class="alert alert-info d-none" role="alert">
                {{ voice_text }}
            </div>
        </div>
    </div>
</template>

<script>
/* eslint-disable */
import { ref, onMounted } from 'vue'
import { useMobile } from "@/store/Mobile"
import { useLanguage } from '@/store/Language'
import { useInitUnit } from "@/store/InitUnit"
import { useSpine } from "@/store/Spine"
import { storeToRefs } from 'pinia'

export default {
    name: 'ToolAccordion',
    components: {
    },
    setup() {
        const { isMobile } = storeToRefs(useMobile())
        const { language, text } = storeToRefs(useLanguage())
        const { unit } = storeToRefs(useInitUnit())

        const voice_lan = ref(2)
        const voice_text = ref("")
        
        var alertOnce = 1
        onMounted(() => {
            const choiceArray = document.querySelectorAll(".choice")
            choiceArray.forEach((card) => {
                card.addEventListener("click", (item) => {
                    if (item.target.classList.contains("small") ||
                        item.target.parentElement.classList.contains("small") ||
                        item.target.parentElement.parentElement.classList.contains("small")) {
                            
                            if (alertOnce) {
                                document.getElementById("soundAlert").classList.remove("d-none")
                                window.setTimeout(function(){
                                    document.getElementById("soundAlert").classList.add("d-none")
                                }, 5000);
                                alertOnce = 0
                            }
                        
                            choiceArray.forEach((element) => {
                                element.classList.remove("expand")
                                element.classList.add('small')
                            })
                            document.querySelectorAll(".small button").forEach((element) => {
                                element.classList.add('btn-hidden')
                            })
                            card.classList.remove("small")
                            card.classList.add('expand')
        
                            window.setTimeout(function(){
                                document.querySelectorAll(".expand button").forEach((element) => {
                                    element.classList.remove('btn-hidden')
                                })
                            }, 1000);
                    }
                });
            });
        })

        const changeAnimation = (ani) => {
            useSpine().changeAnimation(ani)
        }

        const playSound = (sound, txt) => {
            console.log(sound)
            voice_text.value = txt[language.value]

            document.getElementById("voice_text").classList.remove("d-none")

            if (voice_lan.value === 1) {
                var audio = new Audio('voice/' + sound);
                audio.play();
            }else if (voice_lan.value === 2) {
                var audio = new Audio('voice/' + String(sound).replace(".wav", "_jp.wav"));
                audio.play();
            }
        }

        const choose_spine = () => {
            document.getElementById("voice_text").classList.add("d-none")
            document.getElementById("voice_lan").classList.add("d-none")
        }

        const choose_voice = () => {
            document.getElementById("voice_lan").classList.remove("d-none")
        }

        return {
            language,
            isMobile,
            text,
            unit,
            voice_lan,
            voice_text,
            changeAnimation,
            playSound,
            choose_spine,
            choose_voice
        }
    }
}
</script>

<style scoped>
.container{
     display: flex;
     width: 100%;
     padding: 0;
}
.choice{
     height: auto;
     box-sizing: border-box;
     padding: 0;
     /* overflow: hidden; */
     float: left;
     align-items: center;
     transition: width 1s;
     border-radius:3px;
}
.expand{
     width: 85%;
}
.small{
     width: 15%;
     background-color: #ddd !important;
}
.btn-hidden{
    display: none;
}
.div-inline{
    display: inline-block;
}

</style>
